import { createContext, useContext } from "react"
import { HandleOpenModalProps } from "@rp/react/web/common/composite-components"

type QueryBarContextValue = {
  onHandleOpenModal: HandleOpenModalProps
}

const QueryBarContext = createContext<QueryBarContextValue | null>(null)

const QueryBarContextProvider = ({
  children,
  value,
}: {
  children: React.ReactNode
  value: QueryBarContextValue
}) => {
  return <QueryBarContext.Provider value={value}>{children}</QueryBarContext.Provider>
}

const useQueryBarContext = () => {
  const context = useContext(QueryBarContext)
  if (!context) {
    throw new Error("useQueryBarContext must be used within a QueryBarContextProvider")
  }
  return context
}

export { QueryBarContextProvider, useQueryBarContext }
